import React from "react";
import Skeleton from "@material-ui/lab/Skeleton";
import { useEffect, useState } from "react";

const PreProfile = () => {
    const [size, setSize] = useState(window.innerWidth);

   
    return (
        <>
            <div className="profile-loading-wrapper">
                <Skeleton
                    className="mb"
                    animation="wave"
                    variant="circle"
                    width={size / 9}
                    height={size / 9}
                />
                <Skeleton
                    className="mb"
                    animation="wave"
                    variant="h1"
                    width={size / 1.5}
                    height={size / 10}
                />

                <Skeleton
                    animation="wave"
                    variant="rect"
                    width={size / 1.5}
                    height={size / 20}
                />
            </div>
        </>
    );
};

export default PreProfile;
