import React, { useState, useEffect } from "react";
import axios from "axios";
import { makeStyles, withStyles, useTheme } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import AccountCircle from "@material-ui/icons/AccountCircle";
import Avatar from "@material-ui/core/Avatar";
import Badge from "@material-ui/core/Badge";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import CssBaseline from "@material-ui/core/CssBaseline";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";
import Grid from "@material-ui/core/Grid";
import PostAddIcon from "@material-ui/icons/PostAdd";
import clsx from "clsx";
import Profile from "../profile";
import Posts from "../posts";
import PreProfile from "../preloaders/PreProfile";
import "./main.css";

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    root: {
        display: "flex",
        width: "90%",
        height: "100vh",
        alignItems: "center",
        justifyContent: "center",
        margin: "auto",
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
        transition: theme.transitions.create(["width", "margin"], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        background: " rgb(40, 41, 40)",
        color: "#fff",
    },
    appBarShift: {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(["width", "margin"], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: 36,
    },
    hide: {
        display: "none",
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
        whiteSpace: "nowrap",
        background: " rgb(40, 41, 40)",
        color: "#fff",
    },
    drawerOpen: {
        width: drawerWidth,
        transition: theme.transitions.create("width", {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
        background: " rgb(40, 41, 40)",
        color: "#fff",
    },

    drawerClose: {
        transition: theme.transitions.create("width", {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        overflowX: "hidden",
        width: theme.spacing(7) + 1,
        [theme.breakpoints.up("sm")]: {
            width: theme.spacing(9) + 1,
        },
        background: "#1f1f1f",
        color: "#fff",
    },
    toolbar: {
        display: "flex",
        alignItems: "center",
        justifyContent: "flex-end",
        padding: theme.spacing(0, 1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
    },
}));

// --------------- Avatar Style----------
const StyledBadge = withStyles((theme) => ({
    badge: {
        backgroundColor: "#44b700",
        color: "#44b700",
        boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
        "&::after": {
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
            borderRadius: "50%",
            animation: "$ripple 1.2s infinite ease-in-out",
            border: "1px solid currentColor",
            content: '""',
        },
    },
    "@keyframes ripple": {
        "0%": {
            transform: "scale(.8)",
            opacity: 1,
        },
        "100%": {
            transform: "scale(2.4)",
            opacity: 0,
        },
    },
}))(Badge);
const Main = () => {
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(true);
    const theme = useTheme();
    const classes = useStyles();
    const [auth, setAuth] = useState(true);
    const [open, setOpen] = useState(false);

    const [currentPage, setCurrentPage] = useState("profile");

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };
    useEffect(() => {
        const fetchData = async () => {
            try {
                const result = await axios("https://api.randomuser.me/");

                setData(result.data.results[0]);
                setLoading(false);
            } catch (err) {
                console.log(err);
            }
        };
        fetchData();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    return (
        <>
            <Grid container spacing={1}>
                <div className={classes.root}>
                    {loading || !data ? (
                        <Grid item xs={12}>
                            <PreProfile />
                        </Grid>
                    ) : (
                        <>
                            <CssBaseline />
                            <AppBar
                                color="inherit"
                                position="fixed"
                                className={clsx(classes.appBar, {
                                    [classes.appBarShift]: open,
                                })}
                            >
                                <Toolbar>
                                    <IconButton
                                        color="primary"
                                        aria-label="open drawer"
                                        onClick={handleDrawerOpen}
                                        edge="start"
                                        className={clsx(classes.menuButton, {
                                            [classes.hide]: open,
                                        })}
                                    >
                                        <MenuIcon />
                                    </IconButton>
                                    {auth && (
                                        <div>
                                            <IconButton
                                                aria-label="account of current user"
                                                aria-controls="menu-appbar"
                                                aria-haspopup="true"
                                                color="inherit"
                                            >
                                                <StyledBadge
                                                    overlap="circle"
                                                    anchorOrigin={{
                                                        vertical: "bottom",
                                                        horizontal: "right",
                                                    }}
                                                    variant="dot"
                                                >
                                                    <Avatar
                                                        alt={data.name.first}
                                                        src={data.picture.large}
                                                        className={
                                                            classes.large
                                                        }
                                                    />
                                                </StyledBadge>
                                            </IconButton>
                                        </div>
                                    )}
                                    <Typography variant="h6" noWrap>
                                        <span>{data.name.title}. </span>
                                        <span>{data.name.first} </span>
                                        <span>{data.name.last} </span>
                                    </Typography>
                                </Toolbar>
                            </AppBar>
                            <Drawer
                                variant="permanent"
                                className={clsx(classes.drawer, {
                                    [classes.drawerOpen]: open,
                                    [classes.drawerClose]: !open,
                                })}
                                classes={{
                                    paper: clsx({
                                        [classes.drawerOpen]: open,
                                        [classes.drawerClose]: !open,
                                    }),
                                }}
                            >
                                <div className={classes.toolbar}>
                                    <IconButton onClick={handleDrawerClose}>
                                        {theme.direction === "rtl" ? (
                                            <ChevronRightIcon />
                                        ) : (
                                            <ChevronLeftIcon />
                                        )}
                                    </IconButton>
                                </div>
                                <Divider />
                                <List>
                                    <ListItem
                                        button
                                        onClick={() =>
                                            setCurrentPage("profile")
                                        }
                                    >
                                        <ListItemIcon>
                                            <AccountCircle color="primary" />
                                        </ListItemIcon>
                                        <ListItemText primary={"Profile"} />
                                    </ListItem>
                                </List>
                                <Divider />
                                <List>
                                    <ListItem
                                        button
                                        onClick={() => setCurrentPage("posts")}
                                    >
                                        <ListItemIcon>
                                            <PostAddIcon color="primary" />
                                        </ListItemIcon>
                                        <ListItemText primary={"Posts"} />
                                    </ListItem>
                                </List>
                            </Drawer>
                            <main className={classes.content}>
                                <div className={classes.toolbar} />
                            </main>
                            {currentPage === "profile" && (
                                <Profile data={data} />
                            )}
                            {currentPage === "posts" && (
                                <Posts userData={data} />
                            )}
                        </>
                    )}
                </div>
            </Grid>
        </>
    );
};

export default Main;
